import React, { useState } from 'react'
import { profile } from '../../assets'
import { NavLink } from 'react-router-dom'
import './index.scss'
import {
    Row,
    Col,
    Input,
    Button,
    Menu,
    Dropdown,
    Icon,
    Checkbox,
    Drawer
  } from 'antd'
import CustomSearchInput from '../../components/CustomSearchInput'
const { Item } = Menu
const menuItems = ['По алфавиту А-Я', 'По алфавиту А-Я', 'По алфавиту А-Я']

const contacts = [
    {
        name: 'Галина Гамбурцева',
        status: 'на сайте',
        role: 'Администратор',
        email: 'v.pavlova@sigmatr.ru',
        icq: '699442724',
        phone: 'доб. 224',
        city: 'Москва',
        country: 'Россия',
        company: 'ООО «Сигма Трейд»',
        position : 'sales manager',
        img: profile,
    },
    {
        name: 'Галина Гамбурцева',
        status: 'на сайте',
        role: 'Администратор',
        email: 'v.pavlova@sigmatr.ru',
        icq: '699442724',
        phone: 'доб. 224',
        city: 'Москва',
        country: 'Россия',
        company: 'ООО «Сигма Трейд»',
        position : 'sales manager',
        img: profile,
    },
    {
        name: 'Галина Гамбурцева',
        status: 'на сайте',
        role: 'Администратор',
        email: 'v.pavlova@sigmatr.ru',
        icq: '699442724',
        phone: 'доб. 224',
        city: 'Москва',
        country: 'Россия',
        company: 'ООО «Сигма Трейд»',
        position : 'sales manager',
        img: profile,
    }
]

function Contacts(){
    const [menuItem, setMenuItem] = useState(menuItems[0])

  const menu = (
    <Menu>
      {menuItems.map((item, index) => (
        <Item onClick={() => setMenuItem(item)} key={index}>
          <a href="#">{item}</a>
        </Item>
      ))}
    </Menu>
  )
    return (
            <div className="checkout contacts">
                <div className="checkout__row">
                    <div className="checkout__title page-title">
                        <h2>
                            Контакты
                        </h2>
                    </div>
                    <CustomSearchInput />
                </div>
                <div className="contact-tab">
                    <ul className="contact-tab">
                        <li className="contact-tab__item active">
                            <a href="#">
                            сотрудники
                            </a>
                        </li>
                        <li className="contact-tab__item">
                            <a href="#">
                            руководство
                            </a>
                        </li>
                        <li className="contact-tab__item">
                            <a href="#">
                            менеджмент
                            </a>
                        </li>
                    </ul> 
                </div>   
                <div className="checkout__row flex-start">
                    <div className="LowerHeader_Sort">
                        <span> Сортировать: </span>
                        <Dropdown overlay={menu} trigger={['click']}>
                            <a className="ant-dropdown-link">
                            {menuItem} <Icon type="down" />
                            </a>
                        </Dropdown>
                    </div>
                    <div className="contact__block ellipsis-block">
                        <div className="ellipsis">
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                       <p className="list-actions">
                            Действия со списками
                       </p>
                    </div>
                </div>
                <div className="contacts__list">
                    
                    {contacts.map((Contact) => (
                        <div className="contacts__item">
                           <div className="contacts__wrapper">
                                <div className="contacts__block contacts__block-first">
                                    <div className="contacts_img">
                                        <img src={profile} alt=""/>
                                    </div>
                                    <div className="contact__block">
                                        <p className="contacts__text contacts__text-title">
                                            {Contact.name}
                                        </p>
                                        <p className="contacts__text contacts__text-status">
                                            {Contact.status}
                                        </p>
                                        <p className="contacts__text contacts__text-role">
                                            {Contact.role}
                                        </p>
                                    </div>
                                </div>
                                <div className="contacts__block">
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                                E-mail:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                            <p className="contacts__text-bold contacts__text">
                                                {Contact.email}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                                ICQ:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                          <p className="contacts__text-bold contacts__text">
                                                {Contact.icq}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                               Телефон:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                          <p className="contacts__text-bold contacts__text">
                                                {Contact.phone}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                                Город:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                            <p className="contacts__text-bold contacts__text">
                                                {Contact.city}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                               Страна:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                            <p className="contacts__text-bold contacts__text">
                                                {Contact.country}
                                            </p>
                                        </div>
                                    </div>
                                    <div className="contact__row">
                                        <div className="contacts__column">
                                            <p className="contacts__text">
                                                Компания:
                                            </p>
                                        </div>
                                        <div className="contacts__column">
                                            <p className="contacts__text-bold contacts__text">
                                                {Contact.company}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="contacts__block">
                                    <div className="contact__row">
                                        <p className="contacts__text">
                                            Сигма Трейд
                                        </p>
                                    </div>
                                    <div className="contact__row">
                                        <p className="contacts__text contacts__text-bold">
                                            {Contact.position}
                                        </p>
                                    </div>
                                </div>
                           </div>
                        </div>
                    ))}
                </div>
            </div>
    )
}
export default Contacts