import React, { useState } from 'react'
import { profile1,profile2,profile3 } from '../../assets'
import { NavLink } from 'react-router-dom'
import './index.scss'
import {
    Row,
    Col,
    Input,
    Button,
    Menu,
    Dropdown,
    Icon,
    Checkbox,
    Drawer
  } from 'antd'

const profile = [
    {
        name: 'Управление профилем',
        path: '/user-profile/profile',
        img : profile1
    },
    {
        name: 'Мои заказы',
        path: '/user-profile/order',
        img : profile2
    },
    {
        name: 'Корзина',
        path: '/user-profile/basket',
        img : profile3
    }
]

function ProfileControl(){
    return (
            <div className="checkout contacts profile">
                <div className="checkout__row">
                    <div className="checkout__title page-title">
                        <h2>
                            Профиль покупателя
                        </h2>
                    </div>
                </div>
                <div className="contacts__list profile">
                    
                    {profile.map((Profile) => (
                       <NavLink className="contacts__item" to={Profile.path}>
                        
                           <div className="contacts__wrapper">
                                <div className="contacts__block">
                                    <div className="contacts_img">
                                        <img src={Profile.img} alt=""/>
                                    </div>
                                </div>
                                <div className="contacts__block">
                                    <p className="contacts__text contacts__text-title">
                                            {Profile.name}
                                    </p>
                                </div>
                           </div>
                        
                       </NavLink>
                    ))}
                </div>
            </div>
    )
}
export default ProfileControl