import React from 'react'
import './index.scss'
import { promotion } from '../../assets'
const promotions = [
  {
      title: 'Бельгийская посуда Berghoff в подарок',
      date: '02.11.2019',
      desc: 'С 01.11.2019 по 30.12.2019 При покупке комплекта FORNELLI из трех единиц набор бельгийской посуды Berghoff - в подарок',
      img: promotion,
  },
  {
      title: 'Бельгийская посуда Berghoff в подарок',
      date: '02.11.2019',
      desc: 'С 01.11.2019 по 30.12.2019 При покупке комплекта FORNELLI из трех единиц набор бельгийской посуды Berghoff - в подарок',
      img: promotion,
  }
]
function Promotion({ data }) {
  const { title, date, desc, img } = data
  return (
    <div className="promotion">
      <div className="promotion__img">
          <img src={img} alt=""/>
      </div>
      <div className="promotion__date">
          <p>
          {date}
          </p>
      </div>
      <div className="promotion__title">
        <h2>
          {title}
        </h2>
      </div>
      <div className="promotion__desc">
        <p>
          {desc}
        </p>
      </div>
    </div>
  )
}

export default Promotion
