import React, { useState } from 'react'
import { group } from '../../assets'
import { NavLink } from 'react-router-dom'
import './index.scss'
import {
    Row,
    Col,
    Input,
    Button,
    Menu,
    Dropdown,
    Icon,
    Checkbox,
    Drawer
  } from 'antd'
import CustomSearchInput from '../../components/CustomSearchInput'
const { Item } = Menu
const menuItems = ['По алфавиту', 'По алфавиту', 'По алфавиту']

const groups = [
    {
        name: 'Портал (кухни)',
        desc: 'Какое-то описание',
        count: '320 участников',
        img: group,
    },
    {
        name: 'Портал (кухни)',
        desc: 'Какое-то описание',
        count: '320 участников',
        img: group,
    },
    {
        name: 'Портал (кухни)',
        desc: 'Какое-то описание',
        count: '320 участников',
        img: group,
    },
    {
        name: 'Портал (кухни)',
        desc: 'Какое-то описание',
        count: '320 участников',
        img: group,
    }
]

function Groups(){
    const [menuItem, setMenuItem] = useState(menuItems[0])

  const menu = (
    <Menu>
      {menuItems.map((item, index) => (
        <Item onClick={() => setMenuItem(item)} key={index}>
          <a href="#">{item}</a>
        </Item>
      ))}
    </Menu>
  )
    return (
            <div className="checkout contacts group">
                <div className="checkout__row">
                    <div className="checkout__title page-title">
                        <h2>
                            Группы и проекты
                        </h2>
                    </div>
                    <CustomSearchInput />
                </div>
                <div className="checkout__row flex-start">
                    <div className="LowerHeader_Sort">
                        <span> Сортировать: </span>
                        <Dropdown overlay={menu} trigger={['click']}>
                            <a className="ant-dropdown-link">
                            {menuItem} <Icon type="down" />
                            </a>
                        </Dropdown>
                    </div>
                </div>
                <div className="contacts__list group">
                    
                    {groups.map((Group) => (
                        <div className="contacts__item">
                           <div className="contacts__wrapper">
                                <div className="contacts__block contacts__block-first">
                                    <div className="contacts_img">
                                        <img src={group} alt=""/>
                                    </div>
                                </div>
                                <div className="contacts__block">
                                    <p className="contacts__text contacts__text-title">
                                            {Group.name}
                                    </p>
                                    <p className="contacts__text group-margin">
                                        {Group.desc}
                                    </p>
                                    <p className="contacts__text contacts__text-role">
                                        {Group.count}
                                    </p>
                                </div>
                           </div>
                        </div>
                    ))}
                </div>
            </div>
    )
}
export default Groups